import React from 'react';
import PropTypes from 'prop-types';

function MapSubfilter(props) {
  const handleValueChange = evt => {
    const { id, value } = evt.target; // <-- moved outside asynchronous context
    props.onValueChange({ id: id, value: value });
  };

  return (
    <React.Fragment>
      <label htmlFor={props.item.id} className="mapSubfilterLbl">
        {props.item.name}
      </label>
      <select
        className="mapSubfilterSel"
        name={props.item.id}
        id={props.item.id}
        defaultValue={props.selected}
        onChange={handleValueChange}
      >
        {props.item.values.map(code => (
          <option key={code.id} value={code.id}>
            {code.name}
          </option>
        ))}
      </select>
    </React.Fragment>
  );
}

MapSubfilter.propTypes = {
  selected: PropTypes.string,
  item: PropTypes.object,
  onValueChange: PropTypes.func,
};

export default MapSubfilter;
