import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

import { getMapSettings } from '../../lib/settings';
import MapFilterGroup from './MapFilterGroup';
import { CHANGE_MAP_FILTERS } from '../../ducks/sdmx';

class Map extends React.Component {
  map_styles = {
    poly_disabled: {
      weight: 1,
      fillOpacity: 0.1,
      color: '#000000',
    },
    poly_highlight: {
      weight: 3,
    },
    poly_enabled: {
      weight: 1,
      fillOpacity: 0.1,
      color: '#0297C9',
    },
    opacity_range: {
      min: 0.1,
      max: 0.8,
    },
  };

  updateSel = newsel => {
    this.props.changeFilter({ id: newsel.id, val: newsel.value });
  };

  //Returns the value from the refarea and the list of selected codes
  getValue = (refarea, selection) => {
    var key = []; //the key to access the value in the sdmx json
    var ret = {};
    var code = '';
    //foreach dimension
    for (var i = 0; i < this.props.sdmx.structure.dimensions.observation.length; i++) {
      let d = this.props.sdmx.structure.dimensions.observation[i];
      if (d.id == getMapSettings.ref_area_dim_id) {
        //is the geo dimension? Assign it
        code = refarea;
      } else {
        //It is not the geo dimension get the selected code
        code = selection[d.id];
      }

      //get the index of the code
      let idx = d.values.findIndex(i => i.id == code);
      if (idx == -1) return null;
      //assign name/label to the return obj
      ret[d.id] = { name: d.values[idx].name, id: d.values[idx].id };
      key.push(idx); //continue to compose the SDMX value access key
    }

    //Access the value and assign it to the ret obj
    key = key.join(':');
    ret['obs_val'] = this.props.sdmx.dataSets[0].observations[key];
    return ret;
  };

  //Get all the values for the selected geo areas and the unique selection of the other dims
  getFilteredVals = () => {
    if (typeof this.props == 'undefined' || !this.props) return null;
    if (typeof this.props.sdmx == 'undefined' || !this.props.sdmx) return null;

    let vals = [];
    let geoDim = this.props.sdmx.structure.dimensions.observation.find(
      i => i.id == getMapSettings.ref_area_dim_id,
    );
    geoDim.values.forEach(v => {
      vals.push(this.getValue(v.id, this.props.selection));
    });
    return vals;
  };

  scaleOpacity = val => {
    //Opacity is now between 0 and 1
    var opacity = (val - this.valMin) / this.valRange;
    //rescale between min and max opacity
    opacity =
      opacity * (this.map_styles.opacity_range.max - this.map_styles.opacity_range.min) +
      this.map_styles.opacity_range.min;
    return opacity;
  };

  onEachLayer = layer => {
    var val = this.getValue(layer.feature.properties.ISO3_CODE, this.props.selection);

    //The calculated_style attrib is used to reassign the values after the on_mouse_over
    if (val && typeof val.obs_val != 'undefined') {
      var scaledOpacity = 0.5;
      if (!isNaN(val.obs_val[0])) {
        scaledOpacity = this.scaleOpacity(val.obs_val[0]);
      }

      //clone
      var range_style = JSON.parse(JSON.stringify(this.map_styles.poly_enabled));
      range_style.fillOpacity = scaledOpacity;
      layer.setStyle(range_style);
      layer.feature.properties['calculated_style'] = range_style;
    } else {
      layer.setStyle(this.map_styles.poly_disabled);
      layer.feature.properties['calculated_style'] = this.map_styles.poly_disabled;
    }

    layer.feature.properties['values'] = val;
  };

  highlighFeature = e => {
    var layer = e.target;
    layer.setStyle(this.map_styles.poly_highlight);
    if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
      layer.bringToFront();
    }

    this.infobox.update(layer.feature.properties);
  };

  resetHighlight = e => {
    var layer = e.target;
    if (layer.feature.properties.calculated_style) {
      layer.setStyle(layer.feature.properties.calculated_style);
    }
    this.infobox.update();
  };

  zoomToFeature = e => {
    this.map.fitBounds(e.target.getBounds());
  };

  onEachFeature = (feature, layer) => {
    layer.on('mouseover', this.highlighFeature);
    layer.on('mouseout', this.resetHighlight);
    layer.on('click', this.zoomToFeature);
  };

  componentDidMount() {
    this.map = L.map('de_map', {
      center: [0, 0],
    });
    this.countryLayer = L.geoJSON(this.props.shape, {
      style: this.map_styles.poly_disabled,
      onEachFeature: this.onEachFeature,
    }).addTo(this.map);

    this.infobox = L.control();

    this.infobox.onAdd = function(map) {
      this._div = L.DomUtil.create('div', 'map_info');
      this.update();
      return this._div;
    };

    this.infobox.update = function(props) {
      if (!props) {
        this._div.innerHTML = '<b>Hover on any active country</b>';
        return;
      }
      var val = '';
      if (props.values) {
        const dim_sel = [];
        for (let k in props.values) {
          if (
            props.values.hasOwnProperty(k) &&
            k != getMapSettings.ref_area_dim_id &&
            k != 'obs_val'
          ) {
            dim_sel.push(props.values[k].name);
          }
        }
        //time = props.values.TIME_PERIOD.name;
        val = props.values.obs_val[0];
        this._div.innerHTML =
          '<b>' +
          props.NAME_ENGL +
          '</b>' +
          '<div id="map_vars">' +
          // props.values.map(p=>p.name).join(", ");
          dim_sel.join(', ') +
          '</div>' +
          '<div id="map_val">' +
          val +
          '</div>';
      } else {
        this._div.innerHTML =
          '<b>' +
          props.NAME_ENGL +
          '</b>' +
          '<div id="map_time">Change your filters to get the value</div>' +
          '<div id="map_val"></div>';
      }
    };

    this.infobox.addTo(this.map);

    this.map.fitBounds(this.countryLayer.getBounds());
    this.map.setZoom(1);

    this.countryLayer.eachLayer(this.onEachLayer);

    this.map.invalidateSize();
  }

  render() {
    this.valMin = Number.MAX_VALUE;
    this.valMax = Number.MIN_VALUE;
    this.valRange = this.valMax;

    //Calculate the max/min to scale the colors
    if (typeof this.props.sdmx != 'undefined') {
      let filteredVals = this.getFilteredVals();
      filteredVals.forEach(v => {
        if (typeof v.obs_val != 'undefined' && !isNaN(v.obs_val[0])) {
          var toCompare = parseFloat(v.obs_val[0]);

          if (toCompare < this.valMin) {
            this.valMin = toCompare;
          }
          if (toCompare > this.valMax) {
            this.valMax = toCompare;
          }
        }
      });

      this.valRange = this.valMax - this.valMin;
    }

    if (typeof this.map != 'undefined' && typeof this.props.sdmx != 'undefined') {
      this.countryLayer.eachLayer(this.onEachLayer);
    }

    return (
      <React.Fragment>
        <MapFilterGroup
          filters={this.props.filters}
          selection={this.props.selection}
          onValueChange={this.updateSel}
        />
        <div id="de_map" />
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  let filters = [];
  if (typeof state.sdmx != 'undefined') {
    for (var i = 0; i < state.sdmx.data.structure.dimensions.observation.length; i++) {
      let d = state.sdmx.data.structure.dimensions.observation[i];
      //the filters: all the dimensions but the geo one
      if (d.id != getMapSettings.ref_area_dim_id) {
        filters.push(d);
      }
    }
  }

  return {
    shape: state.sdmx.map,
    sdmx: state.sdmx.data,
    filters: filters,
    selection: state.sdmx.map_selection,
  };
};

function mapDispatchToProps(dispatch, props) {
  return {
    changeFilter: filter =>
      dispatch({
        type: CHANGE_MAP_FILTERS,
        payload: { filter },
      }),
  };
}

Map.propTypes = {
  shape: PropTypes.object,
  sdmx: PropTypes.object,
  filters: PropTypes.array,
  selection: PropTypes.object,
  changeFilter: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(Map);
